.logo{
    width: 100px;
    margin-top: 12px;
}
.container-logo{
    margin-right: 22%;
    padding-left: 2%;
}

.header{
    display: flex;
    justify-content:flex-start;
    border: 1px solid black;
}



.nav-items >ul{
    list-style: none;
    font-size: 28px;
    font-weight: 400;
    display: flex;
    font-family: system-ui;
}

.nav-items>ul>li{
    padding: 25px;
}

.nav-items>ul>li>a{
    text-decoration: none;
    color: black;
}

.food-card>a{
    text-decoration: none;
}
.food-photo{
    width:220px;
    height: 180px;
    border-radius: 20px;
}

.body-conatiner{
    margin: 20px;
    padding: 30px;
}
.food-card{
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;

}
.food{
    background-color: #26303005;
    width:260px;
    margin: 10px;
    border-radius: 20px;
}
.food:hover{
    border: 1px solid black;
    cursor:pointer;
    box-shadow: 5px 5px 5px rgb(163, 162, 162);
}
.food-content{
 padding: 20px;   
 font-weight: bold;
 color: rgba(2, 6, 12, 0.75);
 font-family: "system-ui";
}

.filter-btn{
    font-size: medium;
    margin: 0px 15px;
}

.shrimmer-con{
    display: flex;
    flex-wrap: wrap;
}

.shrimmer-card{
    width: 260px;
    background-color: rgb(231, 225, 225);
    margin: 10px;
    margin-top: 50px;
    height: 450px;
    border-radius: 20px;
}

.body-upper{
    display: flex;
}

.search-bar{
    font-size: medium;
    margin:0px 2px;
}
.search-btn{
    font-size: medium;
    margin:0px 6px;
}
.user-card{
    border: 1px solid black;
    padding: 20px;
}